html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #1b1b1b;
  font-size: 1.8rem;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 1.25rem 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 800px;
}

.grafica {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .grafica {
    flex-direction: row;
  }
}
.grafica .grafica-ilustracion {
  display: flex;
  margin: auto 0;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  flex: 0 0 calc(45% - 1rem);
  position: relative;
}
.grafica .grafica-ilustracion button {
  position: absolute;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  border: none;
  transition: all 550ms ease;
}
.grafica .grafica-ilustracion button:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.grafica .grafica-ilustracion button:focus {
  transform: scale(1.1);
}
.grafica .grafica-ilustracion #nivel_1 {
  width: 30%;
  height: 30%;
  background-color: yellow;
}
.grafica .grafica-ilustracion #nivel_2 {
  width: 45%;
  height: 45%;
  background-color: blue;
}
.grafica .grafica-ilustracion #nivel_3 {
  width: 60%;
  height: 60%;
  background-color: red;
}
.grafica .grafica-ilustracion #nivel_4 {
  width: 75%;
  height: 75%;
  background-color: green;
}
.grafica .grafica-ilustracion #nivel_5 {
  width: 90%;
  height: 90%;
  background-color: orange;
}
.grafica .grafica-ilustracion::before {
  content: "";
  display: block;
  padding-top: 100%;
}

.sombra {
  -webkit-box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.2);
  box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.2);
}

.texto-grafica {
  border: 1px solid black;
  flex: 0 0 calc(50% - 1rem);
}